import React, { useEffect, useRef, useState } from 'react';
import { db, auth } from '../firebase';
import { collection, doc, setDoc, getDoc, onSnapshot, getDocs, deleteDoc, updateDoc } from 'firebase/firestore';
import ChatContainerCalls from './ChatContainerCall';

const VideoChatApp = ({ currentUserUID, otherUserUID, onClose, userData }) => {
const localVideoRef = useRef(null);
const remoteVideoRef = useRef(null);

const [peerConnection, setPeerConnection] = useState(null);
const [localStream, setLocalStream] = useState(null);
const [remoteStream, setRemoteStream] = useState(null);
const [roomId, setRoomId] = useState(null);
const [isCreatingRoom, setIsCreatingRoom] = useState(false);
const [isJoiningRoom, setIsJoiningRoom] = useState(false);
const [roomIdToJoin, setRoomIdToJoin] = useState('');

const [roomName, setRoomName] = useState('');
const [roomExists, setRoomExists] = useState(false);
const [showJoinButton, setShowJoinButton] = useState(false);
const [callToJoin, setCallToJoin] = useState(false);
const [isVideoEnabled, setIsVideoEnabled] = useState(false);
const [isAudioEnabled, setIsAudioEnabled] = useState(true);
const [inCall, setInCall] = useState(false);
const [currentUser, setCurrentUser] = useState(null);
const [isCalleeJoined, setIsCalleeJoined] = useState(false);
const [isRemoteVideoAvailable, setIsRemoteVideoAvailable] = useState(false);

const [showChat, setShowChat] = useState(false);







let calleeCandidatesUnsubscribe = null;
let callerCandidatesUnsubscribe = null;

const configuration = {
  iceServers: [
    {
      urls: [
        'stun:stun.l.google.com:19302',
        'stun:stun1.l.google.com:19302',
        'stun:stun2.l.google.com:19302',
        'stun:stun3.l.google.com:19302',
        'stun:stun4.l.google.com:19302',
      ]
    },
    {
      urls: "turn:standard.relay.metered.ca:80",
      username: "9f24237eef08d63be9f50c85",
      credential: "bcSTypx8qHWJ7hH9",
    },
    {
      urls: "turn:standard.relay.metered.ca:80?transport=tcp",
      username: "9f24237eef08d63be9f50c85",
      credential: "bcSTypx8qHWJ7hH9",
    },
    {
      urls: "turn:standard.relay.metered.ca:443",
      username: "9f24237eef08d63be9f50c85",
      credential: "bcSTypx8qHWJ7hH9",
    },
    {
      urls: "turns:standard.relay.metered.ca:443?transport=tcp",
      username: "9f24237eef08d63be9f50c85",
      credential: "bcSTypx8qHWJ7hH9",
    },
  ],
  iceCandidatePoolSize: 10,
};
useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((user) => {
    if (user) {
        setCurrentUser(user); // Set the current user when authenticated
    } else {
        setCurrentUser(null); // Reset the current user if not authenticated
    }
    });

    return () => unsubscribe(); // Unsubscribe from the auth state listener
}, []);

useEffect(() => {
  const openUserMedia = async () => {
    const stream = await navigator.mediaDevices.getUserMedia({
      video: true,
      audio: true,
    });
  
    if (localVideoRef.current) {
      localVideoRef.current.srcObject = stream;
    }
  
    setLocalStream(stream);
    setRemoteStream(new MediaStream());
  
    if (remoteVideoRef.current) {
      remoteVideoRef.current.srcObject = remoteStream;
    }
  
    if (localVideoRef.current && localVideoRef.current.srcObject) { // Add null check for console.log
      console.log("Stream:", localVideoRef.current.srcObject);
    }
  
    setIsVideoEnabled(false); // Add this line
  };
  
  
    openUserMedia();
  
    // Clean up function
    return () => {
      hangUp();
    };
  }, [currentUserUID, otherUserUID]);
  
  



useEffect(() => {
    const checkRoomExists = async () => {
    const roomRef = doc(collection(db, 'rooms'), roomIdToJoin);
    const roomSnapshot = await getDoc(roomRef);
    setRoomExists(roomSnapshot.exists());
    };

    if (roomIdToJoin) {
    checkRoomExists();
    }
}, [roomIdToJoin]);

useEffect(() => {
    // Listen for room creation on the callee side
    const roomsCollection = collection(db, 'rooms');
    const unsubscribe = onSnapshot(roomsCollection, (snapshot) => {
    snapshot.docChanges().forEach((change) => {
        if (change.type === 'added') {
        const roomData = change.doc.data();
        const roomId = change.doc.id;
        const swappedRoomId = `${currentUserUID}_${otherUserUID}`;
        if (roomId === swappedRoomId) {
            setShowJoinButton(true);
            setRoomId(roomId);
            setCallToJoin(true);
        }
        }
    });
    });

    // Clean up the listener on component unmount
    return () => unsubscribe();
}, [currentUserUID, otherUserUID]);

const createRoom = async () => {
    // Set the room ID to otherUserUID_currentUserUID
    const roomId = `${otherUserUID}_${currentUserUID}`;
  
    setIsCreatingRoom(true);
    const roomRef = doc(collection(db, 'rooms'), roomId);
  
    const newPeerConnection = new RTCPeerConnection(configuration);
    setPeerConnection(newPeerConnection);
  
    registerPeerConnectionListeners(newPeerConnection);
  
    localStream.getTracks().forEach(track => {
      newPeerConnection.addTrack(track, localStream);
    });
  
    // Create a reference to the videoCallStatus document
    const videoCallStatusDocRef = doc(db, 'videoCallStatus', roomId);
  
    // First, get the document
    const docSnap = await getDoc(videoCallStatusDocRef);
  
    if (!docSnap.exists()) {
      // If the document doesn't exist, create it
      await setDoc(videoCallStatusDocRef, {
        [currentUserUID]: {
          isEnabled: true,
        },
        [otherUserUID]: {
          isEnabled: true,
        },
      });
    } else {
      // If the document exists, update it
      await updateDoc(videoCallStatusDocRef, {
        [currentUserUID]: {
          isEnabled: true,
        },
        [otherUserUID]: {
          isEnabled: true,
        },
      }, { merge: true });
    }
  
    // Code for collecting ICE candidates
    const callerCandidatesCollection = collection(db, 'rooms', roomRef.id, 'callerCandidates');
  
    newPeerConnection.addEventListener('icecandidate', event => {
      if (!event.candidate) {
        console.log('Got final candidate!');
        return;
      }
      console.log('Got candidate: ', event.candidate);
      if (newPeerConnection.signalingState !== 'closed') {
        setDoc(doc(callerCandidatesCollection), event.candidate.toJSON());
      }
    });
  
    // Code for creating a room
    const offer = await newPeerConnection.createOffer();
    await newPeerConnection.setLocalDescription(offer);
    console.log('Created offer:', offer);
  
    const roomWithOffer = {
      'offer': {
        type: offer.type,
        sdp: offer.sdp,
      },
      'name': roomName, // Add the room name to the room data
    };
    await setDoc(roomRef, roomWithOffer);
    setRoomId(roomRef.id);
    console.log(`New room created with SDP offer. Room ID: ${roomRef.id}`);
  
    // Set remote stream as the srcObject of the remote video element
    remoteVideoRef.current.srcObject = remoteStream;
  
    newPeerConnection.addEventListener('track', event => {
      console.log('Got remote track:', event.streams[0]);
      event.streams[0].getTracks().forEach(track => {
        console.log('Add a track to the remoteStream:', track);
        remoteStream.addTrack(track);
      });
    });
  
    // Listening for remote session description
    onSnapshot(roomRef, async snapshot => {
      const data = snapshot.data();
      if (!newPeerConnection.currentRemoteDescription && data && data.answer) {
        console.log('Got remote description: ', data.answer);
        const rtcSessionDescription = new RTCSessionDescription(data.answer);
        await newPeerConnection.setRemoteDescription(rtcSessionDescription);
      }
    });
  
    // Listen for remote ICE candidates
    calleeCandidatesUnsubscribe = onSnapshot(collection(db, 'rooms', roomRef.id, 'calleeCandidates'), snapshot => {
      snapshot.docChanges().forEach(async change => {
        if (change.type === 'added') {
    
            setIsCalleeJoined(true);
        
          let data = change.doc.data();
          console.log(`Got new remote ICE candidate: ${JSON.stringify(data)}`);
          if (newPeerConnection.signalingState !== 'closed') {
            await newPeerConnection.addIceCandidate(new RTCIceCandidate(data));
          }
        }
      });
    });
    setInCall(true);
    localStream.getVideoTracks()[0].enabled = true;
    setIsVideoEnabled(true);
  };
  

const joinRoom = async () => {
    // Set the room ID to currentUserUID_otherUserUID
    const roomId = `${currentUserUID}_${otherUserUID}`;

    if (roomId) {
    setIsJoiningRoom(true);
    const roomRef = doc(collection(db, 'rooms'), roomId);
    const roomSnapshot = await getDoc(roomRef);

    if (roomSnapshot.exists()) {
        console.log('Got room:', roomSnapshot.data());
        console.log('Create PeerConnection with configuration: ', configuration);
        const newPeerConnection = new RTCPeerConnection(configuration);
        setPeerConnection(newPeerConnection);
        registerPeerConnectionListeners(newPeerConnection);
        localStream.getTracks().forEach(track => {
        newPeerConnection.addTrack(track, localStream);
        });

        // Code for collecting ICE candidates
        const calleeCandidatesCollection = collection(db, 'rooms', roomId, 'calleeCandidates');
        newPeerConnection.addEventListener('icecandidate', event => {
        if (!event.candidate) {
            console.log('Got final candidate!');
            return;
        }
        console.log('Got candidate: ', event.candidate);
        setDoc(doc(calleeCandidatesCollection), event.candidate.toJSON());
        });

        // Set remote stream as the srcObject of the remote video element
        remoteVideoRef.current.srcObject = remoteStream;

        newPeerConnection.addEventListener('track', event => {
        console.log('Got remote track:', event.streams[0]);
        event.streams[0].getTracks().forEach(track => {
            console.log('Add a track to the remoteStream:', track);
            remoteStream.addTrack(track);
        });
        });

        // Code for creating SDP answer
        const offer = roomSnapshot.data().offer;
        console.log('Got offer:', offer);
        await newPeerConnection.setRemoteDescription(new RTCSessionDescription(offer));
        const answer = await newPeerConnection.createAnswer();
        console.log('Created answer:', answer);
        await newPeerConnection.setLocalDescription(answer);

        const roomWithAnswer = {
        answer: {
            type: answer.type,
            sdp: answer.sdp,
        },
        };
        await setDoc(roomRef, roomWithAnswer, { merge: true });

        // Listening for remote ICE candidates
        const callerCandidatesCollection = collection(db, 'rooms', roomId, 'callerCandidates');
        onSnapshot(callerCandidatesCollection, snapshot => {
        snapshot.docChanges().forEach(async change => {
            if (change.type === 'added') {
            let data = change.doc.data();
            console.log(`Got new remote ICE candidate: ${JSON.stringify(data)}`);
            if (newPeerConnection.signalingState !== 'closed') {
                await newPeerConnection.addIceCandidate(new RTCIceCandidate(data));
            }
            }
        });
        });

        // Hide the join room button
        setShowJoinButton(false);
    } else {
        console.log('Room not found');
        setIsJoiningRoom(false);
    }
    } else {
    console.error('Invalid room ID');
    }
    setInCall(true);
    setIsVideoEnabled(true);
    setIsCalleeJoined(true);
};




const hangUp = async () => {
    if (localStream) {
      const tracks = localStream.getTracks();
      tracks.forEach((track) => {
        track.stop();
      });
    }
  
    if (remoteStream) {
      remoteStream.getTracks().forEach((track) => track.stop());
    }
  
    // Delete room on hangup
    if (roomId) {
      const calleeCandidates = await getDocs(
        collection(db, "rooms", roomId, "calleeCandidates")
      );
      calleeCandidates.forEach(async (candidate) => {
        await deleteDoc(candidate.ref);
      });
  
      const callerCandidates = await getDocs(
        collection(db, "rooms", roomId, "callerCandidates")
      );
      callerCandidates.forEach(async (candidate) => {
        await deleteDoc(candidate.ref);
      });
  
      const roomRef = doc(collection(db, "rooms"), roomId);
      await deleteDoc(roomRef);
  
      // Delete videoCallStatus document
      const videoCallStatusDocRef = doc(db, "videoCallStatus", roomId);
      await deleteDoc(videoCallStatusDocRef);
    }
  
    if (peerConnection) {
      peerConnection.close();
    }
  
    if (localVideoRef.current) {
        localVideoRef.current.srcObject = null;
        const profilePictureURL = currentUser?.photoURL || "default-profile-picture.png";
        localVideoRef.current.poster = profilePictureURL;
      }
    
      if (remoteVideoRef.current) {
        remoteVideoRef.current.srcObject = null;
      }
    
  
    if (remoteVideoRef.current) {
      remoteVideoRef.current.srcObject = null;
    }
  
    // Remove the listeners
    if (calleeCandidatesUnsubscribe) {
      calleeCandidatesUnsubscribe();
    }
  
    if (callerCandidatesUnsubscribe) {
      callerCandidatesUnsubscribe();
    }
  
    setIsCreatingRoom(false);
    setIsJoiningRoom(false);
    setRoomId(null);
    setRoomIdToJoin("");
    setIsVideoEnabled(false);
  };
  
  const handleHangUp = () => {
    hangUp();
    onClose();
  };
  


const registerPeerConnectionListeners = (pc) => {
    pc.addEventListener('icegatheringstatechange', () => {
    console.log(`ICE gathering state changed: ${pc.iceGatheringState}`);
    });

    pc.addEventListener('connectionstatechange', () => {
    console.log(`Connection state change: ${pc.connectionState}`);
    });

    pc.addEventListener('signalingstatechange', () => {
    console.log(`Signaling state change: ${pc.signalingState}`);
    });

    pc.addEventListener('iceconnectionstatechange ', () => {
    console.log(`ICE connection state change: ${pc.iceConnectionState}`);
    });
};

const handleMuteAudio = () => {
    if (localStream) {
    localStream.getAudioTracks()[0].enabled = !localStream.getAudioTracks()[0].enabled;
    setIsAudioEnabled(!isAudioEnabled);
    }
};

const handleStopVideo = async () => {
  if (localStream) {
    const videoTrack = localStream.getVideoTracks()[0];
    videoTrack.enabled = !videoTrack.enabled;
    setIsVideoEnabled(!isVideoEnabled);

    // Create a reference to the user status document
    const userStatusDocRef = doc(db, 'videoCallStatus', roomId);

    // Get the current data for the document
    const docSnap = await getDoc(userStatusDocRef);

    if (docSnap.exists()) {
      // Update the isEnabled field for the current user
      await updateDoc(userStatusDocRef, {
        [currentUserUID]: {
          isEnabled: videoTrack.enabled,
        },
      }, { merge: true });
    } else {
      // If the document doesn't exist, create it with the correct fields for both users
      await setDoc(userStatusDocRef, {
        [currentUserUID]: {
          isEnabled: videoTrack.enabled,
        },
        [otherUserUID]: {
          isEnabled: false,
        },
      });
    }

    // Toggle between the local video stream and the user's profile picture
    const localVideoElement = localVideoRef.current;
    if (videoTrack.enabled) {
      localVideoElement.srcObject = localStream;
    } else {
      localVideoElement.srcObject = null;
    }
  }
};

useEffect(() => {
  if (roomId && otherUserUID) {
    const userStatusDocRef = doc(db, 'videoCallStatus', roomId);

    const unsubscribe = onSnapshot(userStatusDocRef, (docSnapshot) => {
      if (docSnapshot.exists()) {
        const otherUserStatus = docSnapshot.data()[otherUserUID];
        if (otherUserStatus && otherUserStatus.isEnabled && remoteStream.getTracks().length > 0) {
          setIsCalleeJoined(true);
        } else {
          setIsCalleeJoined(false);
        }
      } else {
        setIsCalleeJoined(false);
      }
    }, (error) => {
      console.log('Error listening to user status document:', error);
    });

    return () => unsubscribe();
  }
}, [roomId, otherUserUID, remoteStream]);
  

  
  

const isMobile = window.innerWidth <= 768;

const handleCloseVideoCallApp = () => {
  onClose();
};


return (
    <div className='video-chat' >



    <div className="video-chat-header d-none-mobile">

    <h2 className="fw-bold lead mb-3" style={{ fontSize: '2rem' }}>{userData.displayName}</h2>
        <div className="video-go-back-svg">
        <button onClick={onClose}>
        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5}  stroke="currentColor" className="w-6 h-6">
            <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
        </svg>
        </button>
    </div>
    </div>
    <div className="video-chat-container">
        <div className={`chat-container-call ${showChat ? 'show' : ''}`}>
          {showChat && (
            <ChatContainerCalls
              currentUserUID={currentUserUID}
              otherUserUID={otherUserUID}
            />
          )}
        </div>

<div className="video-row">



{isMobile && (
       <div className="mobile-go-back-svg">
       <svg onClick={handleCloseVideoCallApp} xmlns="http://www.w3.org/2000/svg" color= '#212529'  fill="none" viewBox="0 0 24 24" strokeWidth={3} stroke="white" height="45" width="25" style={{ margin: '5px' }}>
         <path strokeLinecap="round" strokeLinejoin="round" d="M15.75 19.5 8.25 12l7.5-7.5" />
       </svg>
     </div>
    )}  
<video
className="local-video"
ref={localVideoRef}
autoPlay
playsInline
muted
style={{ display: isVideoEnabled ? 'block' : 'none' }}
/>
{!isVideoEnabled &&  !isMobile &&(
<div className="profile-picture-container">
  <img
    className="profile-picture"
    src={currentUser?.photoURL || "default-profile-picture.png"}
    alt="Profile Picture"
  />
</div>
)}
<video
  className="remote-video"
  ref={remoteVideoRef}
  autoPlay
  playsInline
  style={{ display: isCalleeJoined ? 'block' : 'none' }}
/>
{!isCalleeJoined && (
  <>
   
    <div className="profile-picture-container">
    {isMobile && (
      <p style={{ textAlign: 'center' }}>{userData?.displayName}</p>
    )}
      <img
        className="profile-picture"
        src={userData.photoURL || "default-profile-picture.png"}
        alt="Profile Picture"
      />
    </div>
  </>
)}



</div>
<div className="name-row d-none-mobile ">
<p>{currentUser?.displayName}</p>
<p>{userData.displayName}</p>

</div>

</div>

{!isMobile && (
    <div className="video-chat-footer">
        <div className="video-input-container">
        <button className="settings-button" onClick={handleMuteAudio}>
            {isAudioEnabled ? (
            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="white">
                <path d="M0 0h24v24H0zm0 0h24v24H0z" fill="none"/>
                <path d="M19 11h-1.7c0 .74-.16 1.43-.43 2.05l1.23 1.23c.56-.98.9-2.09.9-3.28zm-4.02.17c0-.06.02-.11.02-.17V5c0-1.66-1.34-3-3-3S9 3.34 9 5v.18l5.98 5.99zM4.27 3L3 4.27l6.01 6.01V11c0 1.66 1.33 3 2.99 3 .22 0 .44-.03.65-.08l1.66 1.66c-.71.33-1.5.52-2.31.52-2.76 0-5.3-2.1-5.3-5.1H5c0 3.41 2.72 6.23 6 6.72V21h2v-3.28c.91-.13 1.77-.45 2.54-.9L19.73 21 21 19.73 4.27 3z"/>
            </svg>
            ) : (
            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="white">
                <path d="M0 0h24v24H0zm0 0h24v24H0z" fill="none"/>
                <path d="M12 14c1.66 0 2.99-1.34 2.99-3L15 5c0-1.66-1.34-3-3-3S9 3.34 9 5v6c0 1.66 1.34 3 3 3zm5.3-3c0 3-2.54 5.1-5.3 5.1S6.7 14 6.7 11H5c0 3.41 2.72 6.23 6 6.72V21h2v-3.28c3.28-.48 6-3.3 6-6.72h-1.7z"/>
            </svg>
            )}
        </button>
        <button className="settings-button " onClick={handleStopVideo}>
            {isVideoEnabled ? (
            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="white">
                <path d="M0 0h24v24H0zm0 0h24v24H0z" fill="none"/>
                <path d="M21 6.5l-4 4V7c0-.55-.45-1-1-1H9.82L21 17.18V6.5zM3.27 2L2 3.27 4.73 6H4c-.55 0-1 .45-1 1v10c0 .55.45 1 1 1h12c.21 0 .39-.08.54-.18L19.73 21 21 19.73 3.27 2z"/>
            </svg>
            ) : (
            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="white">
                <path d="M0 0h24v24H0zm0 0h24v24H0z" fill="none"/>
                <path d="M17 10.5V7c0-.55-.45-1-1-1H4c-.55 0-1 .45-1 1v10c0 .55.45 1 1 1h12c.55 0 1-.45 1-1v-3.5l4 4v-11l-4 4z"/>
            </svg>
            )}
        </button>
        {!inCall && !callToJoin && !roomId && (
            <button
            className="create-room-button"
            onClick={() => {
                if (localStream) {
                  setIsVideoEnabled(true);
                createRoom();
                }
            }}
            >
            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="white"><path d="M0 0h24v24H0z" fill="none"/>
            <path d="M6.62 10.79c1.44 2.83 3.76 5.14 6.59 6.59l2.2-2.2c.27-.27.67-.36 1.02-.24 1.12.37 2.33.57 3.57.57.55 0 1 .45 1 1V20c0 .55-.45 1-1 1-9.39 0-17-7.61-17-17 0-.55.45-1 1-1h3.5c.55 0 1 .45 1 1 0 1.25.2 2.45.57 3.57.11.35.03.74-.25 1.02l-2.2 2.2z"/>
            </svg>
            </button>
        )}

        {!inCall && showJoinButton && (
         <button
         className="join-room-button"
         onClick={() => joinRoom(setIsCalleeJoined(true))}
       >
              
            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="white">
                <path d="M0 0h24v24H0zm0 0h24v24H0z" fill="none"/>
                <path d="M23.71 16.67C20.66 13.78 16.54 12 12 12 7.46 12 3.34 13.78.29 16.67c-.18.18-.29.43-.29.71 0 .28.11.53.29.71l2.48 2.48c.18.18.43.29.71.29.27 0 .52-.11.7-.28.79-.74 1.69-1.36 2.66-1.85.33-.16.56-.5.56-.9v-3.1c1.45-.48 3-.73 4.6-.73s3.15.25 4.6.72v3.1c0 .39.23.74.56.9.98.49 1.87 1.12 2.66 1.85.18.18.43.28.7.28.28 0 .53-.11.71-.29l2.48-2.48c.18-.18.29-.43.29-.71 0-.27-.11-.52-.29-.7zM21.16 6.26l-1.41-1.41-3.56 3.55 1.41 1.41s3.45-3.52 3.56-3.55zM13 2h-2v5h2V2zM6.4 9.81L7.81 8.4 4.26 4.84 2.84 6.26c.11.03 3.56 3.55 3.56 3.55z"/>
            </svg>
            </button>
        )}

        {inCall && peerConnection && (
            <button className="hang-up-button" onClick={handleHangUp}>
            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="white">
                <path d="M0 0h24v24H0z" fill="none"/>
                <path d="M12 9c-1.6 0-3.15.25-4.6.72v3.1c0 .39-.23.74-.56.9-.98.49-1.87 1.12-2.66 1.85-.18.18-.43.28-.7.28-.28 0-.53-.11-.71-.29L.29 13.08c-.18-.17-.29-.42-.29-.7 0-.28.11-.53.29-.71C3.34 8.78 7.46 7 12 7s8.66 1.78 11.71 4.67c.18.18.29.43.29.71 0 .28-.11.53-.29.71l-2.48 2.48c-.18.18-.43.29-.71.29-.27 0-.52-.11-.7-.28-.79-.74-1.69-1.36-2.67-1.85-.33-.16-.56-.5-.56-.9v-3.1C15.15 9.25 13.6 9 12 9z"/>
            </svg>
            </button>
        )}
  <div className="settings-button-container">
  <button className="settings-button" onClick={() => setShowChat(!showChat)}>
  <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="white">
    <path strokeLinecap="round" strokeLinejoin="round" d="M12 20.25c4.97 0 9-3.694 9-8.25s-4.03-8.25-9-8.25S3 7.444 3 12c0 2.104.859 4.023 2.273 5.48.432.447.74 1.04.586 1.641a4.483 4.483 0 0 1-.923 1.785A5.969 5.969 0 0 0 6 21c1.282 0 2.47-.402 3.445-1.087.81.22 1.668.337 2.555.337Z" />
  </svg>
</button>

        </div>
        </div>

    </div>
        )}
            {isMobile && (
    <div className="video-chat-footer-mobile">
<div className="video-chat-footer">
        <div className="video-input-container">
        <button className="settings-button " onClick={handleStopVideo}>
            {isVideoEnabled ? (
            <svg xmlns="http://www.w3.org/2000/svg" height="32px" viewBox="0 0 24 24" width="32px" fill="white">
                <path d="M0 0h24v24H0zm0 0h24v24H0z" fill="none"/>
                <path d="M21 6.5l-4 4V7c0-.55-.45-1-1-1H9.82L21 17.18V6.5zM3.27 2L2 3.27 4.73 6H4c-.55 0-1 .45-1 1v10c0 .55.45 1 1 1h12c.21 0 .39-.08.54-.18L19.73 21 21 19.73 3.27 2z"/>
            </svg>
            ) : (
            <svg xmlns="http://www.w3.org/2000/svg" height="32px" viewBox="0 0 24 24" width="32px" fill="white">
                <path d="M0 0h24v24H0zm0 0h24v24H0z" fill="none"/>
                <path d="M17 10.5V7c0-.55-.45-1-1-1H4c-.55 0-1 .45-1 1v10c0 .55.45 1 1 1h12c.55 0 1-.45 1-1v-3.5l4 4v-11l-4 4z"/>
            </svg>
            )}
        </button>
    
        {!inCall && !callToJoin && !roomId && (
            <button
            className="create-room-button"
            onClick={() => {
                if (localStream) {
                createRoom();
                }
            }}
            >
            <svg xmlns="http://www.w3.org/2000/svg" height="32px" viewBox="0 0 24 24" width="32px" fill="white"><path d="M0 0h24v24H0z" fill="none"/>
            <path d="M6.62 10.79c1.44 2.83 3.76 5.14 6.59 6.59l2.2-2.2c.27-.27.67-.36 1.02-.24 1.12.37 2.33.57 3.57.57.55 0 1 .45 1 1V20c0 .55-.45 1-1 1-9.39 0-17-7.61-17-17 0-.55.45-1 1-1h3.5c.55 0 1 .45 1 1 0 1.25.2 2.45.57 3.57.11.35.03.74-.25 1.02l-2.2 2.2z"/>
            </svg>
            </button>
        )}

        {!inCall && showJoinButton && (
            <button className="join-room-button" onClick={joinRoom}>
            <svg xmlns="http://www.w3.org/2000/svg" height="32px" viewBox="0 0 24 24" width="32px" fill="white">
                <path d="M0 0h24v24H0zm0 0h24v24H0z" fill="none"/>
                <path d="M23.71 16.67C20.66 13.78 16.54 12 12 12 7.46 12 3.34 13.78.29 16.67c-.18.18-.29.43-.29.71 0 .28.11.53.29.71l2.48 2.48c.18.18.43.29.71.29.27 0 .52-.11.7-.28.79-.74 1.69-1.36 2.66-1.85.33-.16.56-.5.56-.9v-3.1c1.45-.48 3-.73 4.6-.73s3.15.25 4.6.72v3.1c0 .39.23.74.56.9.98.49 1.87 1.12 2.66 1.85.18.18.43.28.7.28.28 0 .53-.11.71-.29l2.48-2.48c.18-.18.29-.43.29-.71 0-.27-.11-.52-.29-.7zM21.16 6.26l-1.41-1.41-3.56 3.55 1.41 1.41s3.45-3.52 3.56-3.55zM13 2h-2v5h2V2zM6.4 9.81L7.81 8.4 4.26 4.84 2.84 6.26c.11.03 3.56 3.55 3.56 3.55z"/>
            </svg>
            </button>
        )}

        {inCall && peerConnection && (
            <button className="hang-up-button" onClick={handleHangUp}>
            <svg xmlns="http://www.w3.org/2000/svg" height="32px" viewBox="0 0 24 24" width="32px" fill="white">
                <path d="M0 0h24v24H0z" fill="none"/>
                <path d="M12 9c-1.6 0-3.15.25-4.6.72v3.1c0 .39-.23.74-.56.9-.98.49-1.87 1.12-2.66 1.85-.18.18-.43.28-.7.28-.28 0-.53-.11-.71-.29L.29 13.08c-.18-.17-.29-.42-.29-.7 0-.28.11-.53.29-.71C3.34 8.78 7.46 7 12 7s8.66 1.78 11.71 4.67c.18.18.29.43.29.71 0 .28-.11.53-.29.71l-2.48 2.48c-.18.18-.43.29-.71.29-.27 0-.52-.11-.7-.28-.79-.74-1.69-1.36-2.67-1.85-.33-.16-.56-.5-.56-.9v-3.1C15.15 9.25 13.6 9 12 9z"/>
            </svg>
            </button>
        )}
            <button className="settings-button" onClick={handleMuteAudio}>
            {isAudioEnabled ? (
            <svg xmlns="http://www.w3.org/2000/svg" height="32px" viewBox="0 0 24 24" width="32px" fill="white">
                <path d="M0 0h24v24H0zm0 0h24v24H0z" fill="none"/>
                <path d="M19 11h-1.7c0 .74-.16 1.43-.43 2.05l1.23 1.23c.56-.98.9-2.09.9-3.28zm-4.02.17c0-.06.02-.11.02-.17V5c0-1.66-1.34-3-3-3S9 3.34 9 5v.18l5.98 5.99zM4.27 3L3 4.27l6.01 6.01V11c0 1.66 1.33 3 2.99 3 .22 0 .44-.03.65-.08l1.66 1.66c-.71.33-1.5.52-2.31.52-2.76 0-5.3-2.1-5.3-5.1H5c0 3.41 2.72 6.23 6 6.72V21h2v-3.28c.91-.13 1.77-.45 2.54-.9L19.73 21 21 19.73 4.27 3z"/>
            </svg>
            ) : (
            <svg xmlns="http://www.w3.org/2000/svg" height="32px" viewBox="0 0 24 24" width="32px" fill="white">
                <path d="M0 0h24v24H0zm0 0h24v24H0z" fill="none"/>
                <path d="M12 14c1.66 0 2.99-1.34 2.99-3L15 5c0-1.66-1.34-3-3-3S9 3.34 9 5v6c0 1.66 1.34 3 3 3zm5.3-3c0 3-2.54 5.1-5.3 5.1S6.7 14 6.7 11H5c0 3.41 2.72 6.23 6 6.72V21h2v-3.28c3.28-.48 6-3.3 6-6.72h-1.7z"/>
            </svg>
            )}
        </button>

        </div>

    </div>
    </div>
    )}
    </div>
);

};
export default VideoChatApp;