import React, { useState, useEffect } from 'react';
import { collection, getDoc, doc, updateDoc } from 'firebase/firestore';
import { Link } from 'react-router-dom';
import { firestore, auth } from '../firebase';

import Fuse from 'fuse.js';

const Contacts = ({ toggleRightColumnVisibility }) => {
  const [showContacts, setShowContacts] = useState(false);
  const [contacts, setContacts] = useState([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [searchResults, setSearchResults] = useState([]);

  useEffect(() => {
    const fetchContacts = async () => {
      const authUser = auth.currentUser;
      if (!authUser) {
        console.error('User is not authenticated');
        return;
      }

      let userDocument;

      // Check if the authenticated user is in userProfiles or expertApplications
      const userProfilesCollection = collection(firestore, 'userProfiles');
      const expertApplicationsCollection = collection(firestore, 'expertApplications');
      const userProfileDoc = doc(userProfilesCollection, authUser.uid);
      const expertApplicationDoc = doc(expertApplicationsCollection, authUser.uid);

      const userProfileSnapshot = await getDoc(userProfileDoc);
      const expertApplicationSnapshot = await getDoc(expertApplicationDoc);

      if (userProfileSnapshot.exists()) {
        userDocument = userProfileDoc;
      } else if (expertApplicationSnapshot.exists()) {
        userDocument = expertApplicationDoc;
      } else {
        console.error('User document not found in either userProfiles or expertApplications');
        return;
      }

      const userDocRef = doc(firestore, userDocument.path);
      const userDocSnap = await getDoc(userDocRef);

      if (userDocSnap.exists()) {
        const userData = userDocSnap.data();
        const contactsData = userData.contacts || {};

        // Fetch contact details for each contact UID
        const contactPromises = Object.keys(contactsData).map(async (contactUID) => {
          const contactSnapshot = await getDoc(doc(firestore, 'userProfiles', contactUID));

          if (contactSnapshot.exists()) {
            return { ...contactSnapshot.data(), otherUserUID: contactSnapshot.id };
          }

          const expertSnapshot = await getDoc(doc(firestore, 'expertApplications', contactUID));

          if (expertSnapshot.exists()) {
            return { ...expertSnapshot.data(), otherUserUID: expertSnapshot.id };
          }

          return null;
        });

        const fetchedContacts = await Promise.all(contactPromises);
        setContacts(fetchedContacts.filter(Boolean)); // Filter out null values
      }
    };

    fetchContacts();
  }, []);

  useEffect(() => {
    if (searchQuery.trim() !== '') {
      const fuseOptions = {
        keys: ['displayName', 'occupation', 'keywords'],
        includeScore: true,
        includeMatches: true,
        threshold: 0.4,
      };
  
      const fuse = new Fuse(contacts, fuseOptions);
      const searchResults = fuse.search(searchQuery).map((result) => result.item);
  
      setSearchResults(searchResults);
    } else {
      setSearchResults(contacts);
    }
  }, [searchQuery, contacts]);
  

  const handleSearchInputChange = (e) => {
    setSearchQuery(e.target.value);
  };

  const handleSearchKeyPress = (e) => {
    if (e.key === 'Enter') {
      toggleRightColumnVisibility();
    }
  };

  const handleButtonClick = async (result) => {
    toggleRightColumnVisibility();

    try {
      const expertAppsRef = collection(firestore, 'expertApplications');
      const expertDocSnap = await getDoc(doc(expertAppsRef, result.otherUserUID));

      console.log('Expert Doc Snap:', expertDocSnap.data());

      if (expertDocSnap.exists()) {
        const customURL = expertDocSnap.data().customURL;
        console.log('Custom URL:', customURL);
        window.location.href = `/expert/${customURL}`; // Use React Router's Link path
      } else {
        const userProfilesRef = collection(firestore, 'userProfiles');
        const userDocSnap = await getDoc(doc(userProfilesRef, result.otherUserUID));

        console.log('User Doc Snap:', userDocSnap.data());

        if (userDocSnap.exists()) {
          const customURL = userDocSnap.data().customURL;
          console.log('Custom URL:', customURL);
          window.location.href = `/user/${customURL}`; // Use React Router's Link path
        } else {
          console.log('User profile not found for the specified UID.');
        }
      }
    } catch (error) {
      console.error('Error handling button click:', error.message);
    }
  };

  const handleDeleteContact = async (contactUID) => {
    try {
      const authUser = auth.currentUser;
      if (!authUser) {
        console.error('User is not authenticated');
        return;
      }
  
      let userDocument;
  
      // Check if the authenticated user is in userProfiles or expertApplications
      const userProfilesCollection = collection(firestore, 'userProfiles');
      const expertApplicationsCollection = collection(firestore, 'expertApplications');
      const userProfileDoc = doc(userProfilesCollection, authUser.uid);
      const expertApplicationDoc = doc(expertApplicationsCollection, authUser.uid);
  
      const userProfileSnapshot = await getDoc(userProfileDoc);
      const expertApplicationSnapshot = await getDoc(expertApplicationDoc);
  
      if (userProfileSnapshot.exists()) {
        userDocument = userProfileDoc;
      } else if (expertApplicationSnapshot.exists()) {
        userDocument = expertApplicationDoc;
      } else {
        console.error('User document not found in either userProfiles or expertApplications');
        return;
      }
  
      const userDocRef = doc(firestore, userDocument.path);
      const userDocSnap = await getDoc(userDocRef);
  
      if (userDocSnap.exists()) {
        const userData = userDocSnap.data();
        const contactsData = userData.contacts || {};
  
        // Remove the contactUID from the user's contacts
        delete contactsData[contactUID];
  
        // Update the user's document in Firestore
        await updateDoc(userDocRef, { contacts: contactsData });
  
        // Update the local state after deleting the contact from the user's contacts in Firestore
        setContacts(contacts.filter((contact) => contact.otherUserUID !== contactUID));
        setSearchResults(searchResults.filter((result) => result.otherUserUID !== contactUID));
      }
    } catch (error) {
      console.error('Error deleting contact:', error.message);
    }
  };
  
  
  return (
    <div className="d-flex flex-column gap-2 mt-3">
      <div className="input-container">
        <input
          type="text"
          className="chat-search-input"
          placeholder="Search for Experts"
          value={searchQuery}
          onChange={handleSearchInputChange}
          onKeyDown={handleSearchKeyPress}
        />
      </div>
      <div className="contacts-list">
      {searchResults.map((result, index) => (
  <Link key={index}>
    <button
     className="chatbutton-history"
      onClick={() => handleButtonClick(result)}
    >
       <div className='d-flex'>
          <div className="small-avatar">
            <img
              src={result.photoURL || 'avatar.png'}
              alt="Avatar"
              className="w-100 h-auto rounded-circle"
              style={{ maxWidth: '12rem' }}
            />
        </div>
            <div className='button-align'>
              <p className="font-weight-bold mb-0 text-start ms-2">{result.displayName}</p>
              <p className="font-weight-bold mb-0 ms-2 text-muted">{result.occupation}</p>
            </div>
            </div>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              color="#212529"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1}
              stroke="currentColor"
              height="25"
              width="25"
              onClick={(e) => {
                e.preventDefault(); // Prevent the default behavior, which may cause page reload
                e.stopPropagation(); // Prevent the parent button's onClick event from being triggered
                handleDeleteContact(result.otherUserUID);
              }}
              >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="m14.74 9-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 0 1-2.244 2.077H8.084a2.25 2.25 0 0 1-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 0 0-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 0 1 3.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 0 0-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 0 0-7.5 0"
              />
            </svg>

    </button>
  </Link>
))}

      </div>
    </div>
  );
};

export default Contacts;
