import React, { useState, useEffect } from 'react';
import { db, collection, doc, getDoc, getDocs, onSnapshot, query, where } from '../firebase';
import { auth } from '../firebase';
import { deleteDoc } from 'firebase/firestore';

const ChatHistory = ({ isActive, toggleRightColumnVisibility, updateOtherUserID, otherUserUID }) => {
  const [showChatHistory, setShowChatHistory] = useState(false);
  const [currentUserUID, setCurrentUserUID] = useState('');
  const [otherUserIDs, setOtherUserIDs] = useState([]);
  const [chatsWithUserData, setChatsWithUserData] = useState([]);
  const [userProfilesCache, setUserProfilesCache] = useState({});
  const [hasNewMessages, setHasNewMessages] = useState(false);
  const [hasNewChats, setHasNewChats] = useState(false);
  const [unreadMessages, setUnreadMessages] = useState({});
  const [callingUserID, setCallingUserID] = useState(null);
  const [latestMessageTimes, setLatestMessageTimes] = useState({});

  useEffect(() => {
    const isMobile = window.matchMedia('(max-width: 768px)').matches;
    if (!isMobile) {
      // Run the updateOtherUserID logic only on desktop
      if (!otherUserUID && chatsWithUserData.length > 0) {
        const firstChat = chatsWithUserData[0];
        const firstChatOtherUserID = firstChat?.otherUserID || null;
        updateOtherUserID(firstChatOtherUserID, currentUserUID);
      }
    }
  }, [chatsWithUserData, otherUserUID, currentUserUID, updateOtherUserID]);

  useEffect(() => {
    const fetchChatsForCurrentUser = async (authUID) => {
      const chatsCollection = collection(db, 'chats');
      try {
        const unsubscribe = onSnapshot(chatsCollection, (querySnapshot) => {
          const chats = [];
          querySnapshot.forEach((doc) => {
            const chatID = doc.id;
            const [fetchedAuthUID, fetchedOtherUserID] = chatID.split('_');
            if (fetchedAuthUID === authUID || fetchedOtherUserID === authUID) {
              const otherUserID = fetchedAuthUID === authUID ? fetchedOtherUserID : fetchedAuthUID;
              setOtherUserIDs((prevUserIDs) => [...prevUserIDs, otherUserID]);
              const lastMessage = doc.data().messages ? doc.data().messages[doc.data().messages.length - 1] : null;
              const lastMessageTime = lastMessage ? lastMessage.timestamp.toMillis() : null;
              chats.push({ id: doc.id, data: doc.data(), otherUserID, lastMessageTime });
            }
          });
          setChatsWithUserData(chats);
        });
        return () => {
          unsubscribe();
        };
      } catch (error) {
        console.error(error);
      }
    };

    const unsubscribe = auth.onAuthStateChanged((user) => {
      if (user) {
        const authUID = user.uid;
        setCurrentUserUID(authUID);
        fetchChatsForCurrentUser(authUID);
      }
    });
    return () => {
      unsubscribe();
    };
  }, []);

  const fetchUserProfiles = async (otherUserID) => {
    if (userProfilesCache[otherUserID]) {
      return userProfilesCache[otherUserID];
    }
    const firestore = db;
    let userData = {};
    try {
      const expertAppsRef = collection(firestore, 'expertApplications');
      const expertAppDoc = doc(expertAppsRef, otherUserID);
      const expertDocSnap = await getDoc(expertAppDoc);
      if (expertDocSnap.exists()) {
        const expertData = expertDocSnap.data();
        userData = {
          photoURL: expertData.photoURL || '',
          displayName: expertData.displayName || 'Unknown',
        };
      } else {
        const userProfilesRef = collection(firestore, 'userProfiles');
        const userProfileDoc = doc(userProfilesRef, otherUserID);
        const docSnap = await getDoc(userProfileDoc);

        if (docSnap.exists()) {
          const userProfileData = docSnap.data();
          userData = {
            photoURL: userProfileData.photoURL || '',
            displayName: userProfileData.displayName || 'Unknown',
          };
        }
      }

      setUserProfilesCache(prevCache => ({
        ...prevCache,
        [otherUserID]: userData,
      }));
    } catch (error) {
      console.error(error);
    }
    return userData;
  };

  useEffect(() => {
    const updateChatsWithUserData = async () => {
      const updatedChats = await Promise.all(
        chatsWithUserData.map(async (chat) => {
          const userData = await fetchUserProfiles(chat.otherUserID);
          return { ...chat, userData };
        })
      );
      setChatsWithUserData(updatedChats);
    };
    if (chatsWithUserData.length > 0) {
      updateChatsWithUserData();
    }
  }, [chatsWithUserData]);

  const handleDeleteChat = async (chatID, event) => {
    event.stopPropagation();
    try {
      await deleteDoc(doc(db, 'chats', chatID));
      updateOtherUserID(null, currentUserUID);
      setChatsWithUserData((prevChats) => prevChats.filter((chat) => chat.id !== chatID));
    } catch (error) {
      console.error(error);
    }
  };

  const handleChatButtonClick = (otherUserID, currentUserID) => {
    updateOtherUserID(otherUserID, currentUserID);
    setShowChatHistory(true);
    toggleRightColumnVisibility();
  };

  const hasUnreadMessages = async (chat, currentUserUID) => {
    try {
      const chatID = chat.id;
      const messagesCollectionRef = collection(db, `chats/${chatID}/messages`);
      const querySnapshot = await getDocs(messagesCollectionRef);
      for (const doc of querySnapshot.docs) {
        const message = doc.data();
        if (message.receiver === currentUserUID && !message.isRead) {
          return true;
        }
      }
      return false;
    } catch (error) {
      return false;
    }
  };

  useEffect(() => {
    const fetchUnreadMessages = async () => {
      const unreadMessages = await Promise.all(chatsWithUserData.map(chat => hasUnreadMessages(chat, currentUserUID)));
      setUnreadMessages(unreadMessages);
    };

    fetchUnreadMessages();
  }, [chatsWithUserData, currentUserUID]);

  useEffect(() => {
    if (currentUserUID) {
      const unsubscribeRooms = onSnapshot(collection(db, 'rooms'), (querySnapshot) => {
        querySnapshot.docChanges().forEach((change) => {
          const doc = change.doc;
          const roomID = doc.id;
          const roomData = doc.data();
          const [user1, user2] = roomID.split('_');

          if (change.type === 'added') {
            // Check if the room contains the current user and has an offer (indicating an ongoing call)
            if ((user1 === currentUserUID || user2 === currentUserUID) && roomData.offer) {
              // Set the calling user ID based on the other user in the room
              setCallingUserID(user1 === currentUserUID ? user2 : user1);
            }
          } else if (change.type === 'removed') {
            // Check if the room that was removed contained the current user
            if (user1 === currentUserUID || user2 === currentUserUID) {
              setCallingUserID(null);
            }
          }
        });
      });

      const unsubscribeCalls = onSnapshot(collection(db, 'calls'), (querySnapshot) => {
        querySnapshot.docChanges().forEach((change) => {
          const doc = change.doc;
          const callID = doc.id;
          const callData = doc.data();
          const [user1, user2] = callID.split('_');

          if (change.type === 'added') {
            // Check if the call contains the current user and has an offer (indicating an ongoing call)
            if ((user1 === currentUserUID || user2 === currentUserUID) && callData.offer) {
              // Set the calling user ID based on the other user in the call
              setCallingUserID(user1 === currentUserUID ? user2 : user1);
            }
          } else if (change.type === 'removed') {
            // Check if the call that was removed contained the current user
            if (user1 === currentUserUID || user2 === currentUserUID) {
              setCallingUserID(null);
            }
          }
        });
      });

      return () => {
        unsubscribeRooms();
        unsubscribeCalls();
      };
    }
  }, [currentUserUID]);

  const getLatestMessageTime = async (chat, currentUserId) => {
    if (chat.id) {
      const messagesCollectionRef = collection(db, `chats/${chat.id}/messages`);
      const otherUserId = chat.otherUserID;

      const messagesQuery = query(messagesCollectionRef, where('sender', '==', otherUserId));
      const messagesSnapshot = await getDocs(messagesQuery);

      if (messagesSnapshot.docs.length > 0) {
        const latestMessage = messagesSnapshot.docs.reduce((latest, current) => {
          const latestTime = latest.data().timestamp.toMillis();
          const currentTime = current.data().timestamp.toMillis();
          return currentTime > latestTime ? current : latest;
        });

        // Ensure that the timestamp is parsed properly
        const dateOptions = { year: 'numeric', month: 'short', day: 'numeric' };
        const timeOptions = { hour: 'numeric', minute: 'numeric' };

        const time = latestMessage.data().timestamp.toDate().toLocaleTimeString([], timeOptions); // Convert timestamp to time string
        const date = latestMessage.data().timestamp.toDate().toLocaleDateString([], dateOptions); // Convert timestamp to date string
        return `${time}, ${date}`;
      }
    }
    return '';
  };

  useEffect(() => {
    const fetchLatestMessageTimes = async () => {
      const times = {};

      for (const chat of chatsWithUserData) {
        const time = await getLatestMessageTime(chat, currentUserUID);
        times[chat.id] = time;
      }

      setLatestMessageTimes(times);
    };

    fetchLatestMessageTimes();
  }, [chatsWithUserData, currentUserUID]);

  const sortedChatsWithUserData = chatsWithUserData.sort((a, b) => b.lastMessageTime - a.lastMessageTime);


return (
<div className={isActive ? 'visible' : 'hidden'}>
<div className="p-2 flex flex-col gap-2 h-full">
<div className=" d-flex flex-column gap-2">
<div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
<div>
<h4>Chats</h4>
</div>
<div className="d-flex ">

<button type="button" className="py-3 px-3 gap-4 chat-btn">
<svg xmlns="http://www.w3.org/2000/svg" color= '#212529' fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25">
<path strokeLinecap="round" strokeLinejoin="round" d="M20.25 8.511c.884.284 1.5 1.128 1.5 2.097v4.286c0 1.136-.847 2.1-1.98 2.193-.34.027-.68.052-1.02.072v3.091l-3-3c-1.354 0-2.694-.055-4.02-.163a2.115 2.115 0 01-.825-.242m9.345-8.334a2.126 2.126 0 00-.476-.095 48.64 48.64 0 00-8.048 0c-1.131.094-1.976 1.057-1.976 2.192v4.286c0 .837.46 1.58 1.155 1.951m9.345-8.334V6.637c0-1.621-1.152-3.026-2.76-3.235A48.455 48.455 0 0011.25 3c-2.115 0-4.198.137-6.24.402-1.608.209-2.76 1.614-2.76 3.235v6.226c0 1.621 1.152 3.026 2.76 3.235.577.075 1.157.14 1.74.194V21l4.155-4.155" />
</svg>
</button> 
<button type="button" className="py-3 px-3 gap-4 chat-btn">
<svg xmlns="http://www.w3.org/2000/svg" color= '#212529' fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25">
<path strokeLinecap="round" strokeLinejoin="round" d="M3 13.125C3 12.504 3.504 12 4.125 12h2.25c.621 0 1.125.504 1.125 1.125v6.75C7.5 20.496 6.996 21 6.375 21h-2.25A1.125 1.125 0 013 19.875v-6.75zM9.75 8.625c0-.621.504-1.125 1.125-1.125h2.25c.621 0 1.125.504 1.125 1.125v11.25c0 .621-.504 1.125-1.125 1.125h-2.25a1.125 1.125 0 01-1.125-1.125V8.625zM16.5 4.125c0-.621.504-1.125 1.125-1.125h2.25C20.496 3 21 3.504 21 4.125v15.75c0 .621-.504 1.125-1.125 1.125h-2.25a1.125 1.125 0 01-1.125-1.125V4.125z" />
</svg>
</button>
</div>
</div>
<div className="relative flex flex-col">
<div>
</div>
</div>
{sortedChatsWithUserData.map((chat, index) => (
  <div key={index} className="relative">
  <button
    className="chatbutton-history"
    onClick={() => handleChatButtonClick(chat.otherUserID, currentUserUID)}
  >
    <div className='d-flex'>
      <div className="avatar-wrapper">
        <div className={`avatar-inner ${unreadMessages[index] || callingUserID === chat.otherUserID ? 'unread-border-overlay' : ''}`}>
          <img
            src={chat.userData?.photoURL || `${process.env.PUBLIC_URL}/images/arts/user.webp`}
            alt="Avatar"
            className="small-avatar-mobile rounded-circle"
          />
        </div>
      </div>
      <div className='button-align'>
        <p className="font-weight-bold mb-0 text-start ms-2">{chat.userData?.displayName}</p>
        <p className="text-start ms-2">{latestMessageTimes[chat.id]}</p> {/* Display the timestamp here */}
      </div>
            </div>

      <svg
              xmlns="http://www.w3.org/2000/svg"
              color="#212529"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1}
              stroke="currentColor"
              height="25"
              width="25"
              onClick={(event) => handleDeleteChat(chat.id, event)}
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="m14.74 9-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 0 1-2.244 2.077H8.084a2.25 2.25 0 0 1-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 0 0-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 0 1 3.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 0 0-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 0 0-7.5 0"
              />
            </svg>
            </button>
      </div>
))}



</div>
</div>
</div>
);
};
export default ChatHistory;
